*{
  margin: 0;
  
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{
  background-color: #f3f2ef;
  display: flex;
  flex-direction: column;
  
}

.app_body{
  display: flex;
  margin-top: 35px;
  max-width: device-width;
  margin-left: 20px;
  margin-right: 20px;
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.1px solid lightgray;
  background-color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  z-index: 999;
}
.header_left {
  display: flex;
}

.header_left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}

.header_search {
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  height: 22px;
  color: gray;
  background-color: #eef3f8;
}

.header_search > input {
  outline: none;
  border: none;
  background: none;
}

.header_right {
  display: flex;
}


@media only screen and (max-width:414px) {
 
  
  .header_right {
    display:none;
  }
  
    
}
  
.headerOption{
    display:flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    color:gray;
    cursor: pointer;
}

.headerOption:hover{
    color:black;
}

.headerOption_icon{
    object-fit: contain;
    height: 25px !important;
    width: 25px !important;
}
.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  flex: 0.2 1;
  border-radius: 10px;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.sidebar_avatar {
  margin-bottom: 10px;
}

.sidebar_top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}
.sidebar_top > h2 {
  font-size: 18px;
}
.sidebar_top > h4 {
  color: gray;
  font-size: 12px;
}

.sidebar_top > img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  object-fit: cover;
}

.sidebar_stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.sidebar_stat > p {
  color: gray;
  font-size: 13px;
  font-weight: 600;
}

.sidebar_stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.sidebar_statNumber {
  font-weight: bold;
  color: #0a66c2 !important;
}

.sidebar_bottom {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
    
}

.sidebar_bottom > p {
    font-weight: 600;
    font-size: 13px;
    padding-bottom: 10px;
}

.sidebar_recentItem{
display: flex;
font-size: 13px;
color:gray;
font-weight: bolder;
cursor: pointer;
padding: 5px;
}

.sidebar_recentItem:hover{
    background: whitesmoke;
    border-radius: 10px;
    cursor: pointer;
    color: black;
}

.sidebar_hash{
    margin-left: 5px;
    margin-right: 10px;
}
.feed {
  flex: 0.6 1;
  margin: 0 20px;
}

.feed_inputContainer {
  background-color: white;
  padding: 10px;
  padding-bottom: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.feed_input {
  border: 1px solid lightgray;
  border-radius: 30px;
  display: flex;
  padding: 10px;
  color: gray;
  padding-left: 15px;
}

.feed_input > form {
display: flex;
width: 100%;
}
.feed_input > form > input {
border:none;
flex:1 1;
margin-left: 10px;
outline-width: 0;
font-weight: 600;
}

.feed_input > form > button {
    display: none;
}

.feed_inputOption{
    display: flex;
    justify-content: space-evenly;
}
.inputOption{
    display:flex;
    align-items: center;
    margin-top: 15px;
    color: gray;
    padding: 10px;
    cursor: pointer;

}

.inputOption:hover {
background-color: whitesmoke;
border-radius: 10px;
}

.inputOption > h4 {
    margin-left: 5px;
}
.post {
  background-color: white;
  margin-bottom: 10px;
  border-radius: 10px;
  padding: 15px;
}

.post_header {
  display: flex;
  margin-bottom: 10px;
}

.post_info {
  margin-left: 10px;
}

.post_info > p {
  font-size: 12px;
  color: gray;
}

.post_body {
  overflow-wrap: anywhere;
}

.post_info > h2 {
  font-size: 15px;
}

.post_button{
    display: flex;
    justify-content: space-evenly;
}

.login {
    display : grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 100px;
    cursor: pointer;
}

.login > img {
    object-fit: contain;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.login > form {
    display: flex;
    flex-direction: column;
    cursor: pointer;
}

.login > form > input {
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    cursor: pointer;
}   

.login > form > button {
    width: 365px;
    height: 50px;
    font-size: large;
    color:#fff;
    background-color: #0074b1;
    border-radius: 5px;
    cursor: pointer;
}

.login_register {
    color:#0177b7;
    cursor: pointer;
}

.login > p {
    margin-top: 20px;
}
.widgets{
    position: -webkit-sticky;
    position: sticky;
    top:80px;
    flex:0.2 1;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-bottom: 10px;
}

.widgets_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.widgets_header > h2 {
    font-size: 16px;
    font-weight: 400;
}

.widgets_article{
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widgets_article:hover {
    background-color: whitesmoke;
}

.widgets_articleLeft{
    color:#0177b7;
    margin-right: 5px;
}

.widgets_articleLeft > .MuiSvgIcon-root {
    font-size: 15px;
}
.widgets_articleRight{
    flex:1 1;

}

.widgets_articleRight > h4 {
    font-size: 14px;
}

.widgets_articleLeft > p {
    font-size :12px;
    color: gray;
}
